import { mapState } from 'vuex';
import themeMixin from 'kolibri.coreVue.mixins.themeMixin';

export default {
  name: 'AnswerHistory',
  mixins: [themeMixin],
  props: {
    questionNumber: {
      type: Number,
      required: true,
    },
  },
  computed: {
    ...mapState('examViewer', ['questions']),
    ...mapState({ attemptLogs: 'examAttemptLogs' }),
  },
  methods: {
    questionText(num) {
      return this.$tr('question', { num });
    },
    isAnswered(question) {
      return ((this.attemptLogs[question.exercise_id] || {})[question.question_id] || {}).answer;
    },
    liStyle(index) {
      return {
        backgroundColor:
          this.questionNumber === index ? this.$themeColors.palette.grey.v_200 : '',
      };
    },
  },
  $trs: {
    question: 'Question { num }',
  },
};

