import themeMixin from 'kolibri.coreVue.mixins.themeMixin';
import KTooltip from 'kolibri.coreVue.components.KTooltip';

export default {
  name: 'AnswerIcon',
  components: {
    KTooltip,
  },
  mixins: [themeMixin],
  props: {
    answer: {
      type: String,
      required: true,
      validator(val) {
        return ['right', 'wrong', 'hint', 'rectified'].includes(val);
      },
    },
  },
  computed: {
    tooltipText() {
      switch (this.answer) {
        case 'right':
          return this.$tr('correct');
        case 'wrong':
          return this.$tr('incorrect');
        case 'hint':
          return this.$tr('hintUsed');
        case 'rectified':
          return this.$tr('incorrectFirstTry');
        default:
          return '';
      }
    },
    svgFill() {
      return {
        fill: this.$themeTokens.annotation,
      };
    },
  },
  $trs: {
    correct: 'Correct',
    incorrect: 'Incorrect',
    hintUsed: 'Hint used',
    incorrectFirstTry: 'Incorrect first try',
  },
};

